.App {
  text-align: center;
}
.logo {
  width: 100px;
  margin: 20px 0px;
}
.main {
  width: 100%;
  height: 750px;
  background: url(./spot394.gif) no-repeat center / cover;
}
.row {
  width: 100%;
  margin: 0px auto 50px;
}
.top {
  font-size: 25px;
  font-weight: bold;
  margin: 50px 0px;
}
.col-md-4 {
  cursor: pointer;
}
.col-md-4 h4 {
  font-weight: bold;
  font-size: 16px !important;
  margin: 10px 0px;
  height: 40px;
}
.col-md-4 p {
  font-size: 13px;
  padding: 0px 5px;
  height: 65px;
}
.content {
  font-size: 13px;
  height: 45px;
  padding: 5px;
}
.price {
  color: brown;
  font-weight: bold;
  font-size: 15px;
}
.price span {
  text-decoration: line-through;
  margin-right: 10px;
  font-size: 9px;
  color: black;
  font-weight: normal;
}
img {
  width: 100%;
}
.more {
  width: 55%;
  margin: 100px auto;
}
.more .row {
  gap: 50px;
}
.more .col {
  text-align: left;
}
.more .col h4 {
  padding: 30px 0px;
  border-bottom: 1px solid #ddd;
}
.more .col p:nth-of-type(1) {
  font-size: 15px;
  color: #aaa;
}
.more .col p:nth-of-type(2) {
  font-size: 13px;
  border-bottom: 1px solid #ddd;
}
.more .col button:nth-of-type(3) {
  width: 280px;
}
.tabmenu {
  width: 90%;
  margin: 50px auto;
}
